import { HandlerExternalContext } from '../handler';
import { AbstractCellStyleHandler, HandlerConfig } from './handler.abstract.cell_style';
import { XCellStyle } from '../../table.types';

export class FontStyleHandler extends AbstractCellStyleHandler {
    public name = 'CHANGE_CELL_FONT_STYLE';

    public constructor(externalInterface: HandlerExternalContext, opt: HandlerConfig) {
        super(externalInterface, opt);

        const { style } = opt;

        this.cellStyle = {
            fontStyle: Object.assign({
                fontFamily: '',
                underLine: {
                    width: 0,
                    color: -16777216,
                    style: 'SOLID',
                },
            }, style) as XCellStyle['fontStyle'],
        };
    }

    protected transform2DeaultStyle(cellStyle: Parameters<AbstractCellStyleHandler['transform2DeaultStyle']>[0]) {
        return BI.deepClone({
            fontStyle: cellStyle?.fontStyle,
        });
    }
}
